.footer {
  background-color: #4a4521;
  color: white;
  padding: 10px 0;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  display: flex;
  justify-content: space-around;
}
.footer_title {
  margin: 0px;
  color: white;
  text-decoration: none;
  font-size: 12px;
}
@media (max-width: 400px) {
  .footer {
    flex-direction: column;
  }
  .footer_title {
    margin: 2px;
  }
  .footer_end {
    font-size: 24px; /* Reduced for mobile */

  }
}
